* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
  }
  body{
      height: 100vh;
      background-color: #3e4451;
  }
  .main-container{
    /* padding: 2rem; */
  }

  .header{
      display: flex;
      align-items: center;
      gap:.5rem;
      justify-content: center;
      flex-direction: column;
      color:whitesmoke;
      padding-block: .5rem;
      background-color: #282c34;
  }
  .header-title{
      font-size: 2.4rem;
      color:goldenrod;
      letter-spacing: 0.179em;
  }
  .score-block{
    display: flex;
    gap: 1rem;
    font-size: 20px;
}
  .header-score{
      color: #ffb5b5;
      
  }
  .header-highscore{
    color: #b5ffb5;
  }
  
  .card-container{
      display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px ,1fr));
    grid-template-rows: repeat(2, 1fr);
    gap:15px;
    padding: 2rem;
    /* padding-top: .5rem; */
}
  

  .primarch-container {
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: .5rem;
    border-radius: 1rem;
    gap:.3rem;
    cursor: pointer;
    
  }
  .primarch-image{
    width: 100%;
    border-radius: 1rem;
    
  }
  .primarch-title{
    font-weight: 500;
    color:white;
  }